export const fs = () => getDynamicModule('fs');
export const child_process = () => getDynamicModule('child_process');
export const express = () => getDynamicModule('express');
export const http = () => getDynamicModule('http');
export const https = () => getDynamicModule('https');
export const url = () => getDynamicModule('url');
export const stream = () => getDynamicModule('stream');
export const mimic_response = () => getDynamicModule('mimic-response');

// Since this package is used by both node and the browser, we require all native Node.js modules using this hack
// This keeps webpack quite and works with node.
export const getDynamicModule = (name: string) => eval('require')(name);
